
import {updateucenter,myinfo,getConfig,uploadimg,weixinuploadimg} from '@/api/ucenter.js'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
Toast
},
data () {
return {
showtoast: false,
toastmes:　'',
name: '',
textValue: '',
remnant: 0,
imgsrc: '',
mediaId: ''
}
},
created () {

myinfo().then( res => {
if( res.data.result == 0 ) {
this.userInfo = res.data.datas
this.name = this.userInfo.nickName
this.imgsrc = this.userInfo.headimgurl
}

})

let href = window.location.href.split("#")
let url = href[0]
let data = {
url: url
};

getConfig(data).then(res => {
wx.config({
debug: false,
appId: res.data.appId,
timestamp: res.data.timestamp,
signature: res.data.signature,
nonceStr: res.data.nonceStr,
jsApiList: [
'checkJsApi',
'getLocation',
'chooseImage',
'uploadImage',
'downloadImage'
]
});
});





},
methods: {
getready () {
let _this = this
wx.ready(function(){
_this.getImg()
});
wx.error(function(res){
console.log(res)
});
},
getImg(){
let _this = this
wx.chooseImage({
count: 1,
sizeType: ['original', 'compressed'],
sourceType: ['album', 'camera'],
success: function (res) {
var localIds = res.localIds[0].toString()
_this.uploadImg(localIds)
}
});
},
uploadImg(val) {
let _this = this
wx.uploadImage({
localId: val,
isShowProgressTips: 1,
success: function (res) {
_this.mediaId = res.serverId;

let fdata = {
SERVER_ID:  _this.mediaId
}
weixinuploadimg(fdata).then(res => {
if (res.data.result == 0) {

_this.imgsrc = res.data.datas.headimgurl


}
})


},
fail: function (error) {
picPath = '';
localIds = '';
alert(Json.stringify(error));

}

});
},
descInput(){
var txtVal = this.textValue.length;
this.remnant = txtVal;
},
update () {
if (this.name == '') {
this.showtoast = true
this.toastmes = '请输入姓名'
return false
}

let fdata = {
"nickName": this.name,
"headimgurl": this.imgsrc
}
updateucenter(fdata).then(res => {
if (res.data.result == 0) {
this.$router.go(-1)
}
})
}
}
}



