
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Radio from 'vux/src/components/radio/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import VDistpicker from 'v-distpicker'
import { getAllEmpList,postCategory } from '@/api/employ'
import loadMore from '@/components/loadmore'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import VSite from '@/components/site.vue'
import {changeCurrentUserType} from '@/api/ucenter.js'

export default {
name: 'employ',
components:{
Flexbox,
FlexboxItem,
Tab,
TabItem,
VDistpicker,
XDialog,
XInput,
loadMore,
LoadMore,
Checker,
CheckerItem,
VSite,
Group,
},
data(){
return {
nowIndex: 'no',
barWidth: '0px',
maskdialog: false,
postShow: false,
timeShow: false,
ageShow: false,
hideOnBlur: true,
city: '选择城市',
dialogShow: false,
postlist: [],
timeList: ["全职","兼职","不限"],
allList: [],
orderId: '',
total: 0,
loading: false,
allLoaded: false,
bottomText: '',
endtext: '',
likePosts:[],
min_value: '',
max_value: '',
workType: 0,
nowIndexes: [],
flag: 0, // 0初始 1地址 2岗位 3兼职 4年龄/性别
}
},
created(){

this.getAllList();



postCategory().then(res => {
this.postlist = res.data.datas;
})


},
watch: {
maskdialog (val) {
if (val == true) {
document.body.style.position = 'fixed'
document.body.style.width = '100%'
} else {
document.body.style.position = 'initial'
document.body.style.height = 'auto'
}
}
},
methods: {
tocompany () {
let data = {
currentUserType: 2
}
changeCurrentUserType(data).then( res => {
if (res.data.result == 0) {

}
})

},
resetdata () {
this.reset()
this.flag = 0
this.getAllList()
this.close()
},
close () {
this.maskdialog = false
this.postShow = false;
this.timeShow = false;
this.ageShow = false;

},
reset () {
this.endtext = ''
this.orderId = ''
this.allList = []
this.total = 0
},
loadBottom: function() {


if (!this.endtext) {
this.loading = true;
}
this.orderId = this.allList[this.allList.length - 1].orderId
if (this.orderId == 0) {
this.loading = false;
this.allLoaded = true;
return;
}

if (this.flag == 0) {
this.getAllList();
} else if (this.flag == 1) {
this.chooseCity()

} else if (this.flag == 2) {
this.getPost()

} else if (this.flag == 3) {
this.getimeslect()

}else if (this.flag == 4) {
this.chooseage()
}

},
handleBottomChange(val) {
this.bottomText = val;
},

getAllList(){
let data = {
pagesize: 10,
"pageLastId": this.orderId
};
getAllEmpList(data).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allList.push(item)
})
}, 600)
}
}

})
},

showDialog(){
this.dialogShow = true;
},
closeother () {
this.ageShow = false
this.timeShow = false
this.postShow = false
this.maskdialog = false
},

post(){
this.flag = 2
this.postShow = true;
this.timeShow = false;
this.ageShow = false;
this.maskdialog = true;
this.barWidth = "15px";
},

timeType(){
this.flag = 3
this.postShow = false;
this.timeShow = true;
this.ageShow = false;
this.maskdialog = true;
this.barWidth = "15px";
},

age(){
this.flag = 4
this.postShow = false;
this.timeShow = false;
this.ageShow = true;
this.maskdialog = true;
this.barWidth = "15px";
},

sureAge(){
this.ageShow = false
this.maskdialog = false
if (this.min_value == '' | this.max_value == '') {
return
}
this.reset()

this.chooseage()

},
chooseage () {
let data = {
"pagesize": 10,
"ageStart": this.min_value,
"ageEnd": this.max_value,
"pageLastId": this.orderId,
};
getAllEmpList(data).then(res => {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allList.push(item)
})
}, 600)
}
})
},


getCity(res){
this.flag = 1
this.reset()
this.city = res.city.code;
this.dialogShow = false;

this.chooseCity ()
},
chooseCity () {
let data = {
"pagesize": "10",
"city": this.city,
"pageLastId": this.orderId,
};
getAllEmpList(data).then(res => {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allList.push(item)
})
}, 600)
}
});
},

postSelect(name,id,index){

if (this.nowIndexes.indexOf(index) === -1) {
this.nowIndexes.push(index)
this.likePosts.push(id)
}
else {
this.nowIndexes = this.nowIndexes.filter(el => {
return el != index
})
this.likePosts = this.likePosts.filter(el => {
return el != id
})
}

},
checkActive (index) {
return this.nowIndexes.indexOf(index) !== -1
},
surePost(){


this.postShow = false
this.maskdialog = false
this.flag = 1
if (this.likePosts.length == 0) {
return
}
this.reset()
this.getPost()
},
getPost () {

let data = {
"pagesize": 10,
"jobCategoryIdList": this.likePosts,
"pageLastId": this.orderId,
};
getAllEmpList(data).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total
if (this.orderId == '') {
lists.forEach(item => {
this.allList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allList.push(item)
})
}, 600)
}
}

})
},
timeSelect(item,index){

this.nowIndex = index;
if(item == '全职'){
this.workType = 1
}else if(item == '兼职'){
this.workType = 2
}else{
this.workType = 0
}
},
suretime () {

this.reset()
this.maskdialog = false
this.timeShow = false
this.flag = 3
this.getimeslect()
},
getimeslect () {

let data = {
"pagesize": "10",
"jobTime": this.workType,
"pageLastId": this.orderId,
};
getAllEmpList(data).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allList.push(item)
})
}, 600)
}
}
});

},

goPostDetail(fid){
this.$router.push({name:'employDetail',query:{id: fid}})
}
}
}



