

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Radio from 'vux/src/components/radio/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'

import VDistpicker from 'v-distpicker'
import { allRecruitList,majorFir,majorSec } from '@/api/recruit'
import loadMore from '@/components/loadmore'
import myaddress from '../../../static/pc-code.json'
import VSite from '@/components/site.vue'
import {formatDate} from '@/utils/date.js'
import screening from '@/components/screening.vue'

export default {
name: 'recruitTab',
components:{
Flexbox,
FlexboxItem,
Tab,
TabItem,
VDistpicker,
XDialog,
XInput,
loadMore,
LoadMore,
Checker,
CheckerItem,
VSite,
screening,
Group,
Radio

},
data(){
return{
sexoptions: [{key: '1', value: '男'}, {key: '2', value: '女'},{key: '0', value: '不限'}],
sex:'',
fixedheader:　false,
min_value: '',
max_value:　'',
currentIndex: 'no',
nowIndex: 'no',
barWidth: '0px',
maskdialog: false,
postShow: false,
timeShow: false,
ageShow: false,
hideOnBlur: true,
city: '选择城市',
dialogShow: false,
postlist: [],
timeList: ["全职","兼职","不限"],
allCompList: [],
headimgurl: '',
orderId: '',
total: 0,
loading: false,
allLoaded: false,
bottomText: '',
endtext: '',
majorFirtList: [],
majorSectList: [],
likeMajors:[],
proviceList: [],
cityList: [],
areaData:[],
workType: 0,
flag: 0,  //0表示初始, 1 地址 ，2 岗位，3 兼职
}
},
created(){

this.proviceList = Object.values(myaddress)

for(let i = 0; i<this.proviceList.length; i++){
this.cityList.push(this.proviceList[i].children)
};

this.getrecruiment();

majorFir().then(res => {
this.majorFirtList = res.data.datas;
if(res.data.message == 'success'){
for(let i=0; i<this.majorFirtList.length; i++){
let pid = this.majorFirtList[i].id;
majorSec(pid).then(res => {
this.majorSectList.push(res.data.datas);
});
}

}
});
},
filters: {
formatDate (time) {
let date = new Date(time)
return formatDate(date, 'yyyy-MM-dd')
}
},
mounted () {
let scrollDom = document.querySelector('.rw_content')
scrollDom.addEventListener('scroll', this.handle)
},
destroyed () {
let scrollDom = document.querySelector('.rw_content')
window.removeEventListener('scroll', this.handle)
},
watch: {
maskdialog (val) {
if (val == true) {
document.body.style.position = 'fixed'
document.body.style.width = '100%'
} else {
document.body.style.position = 'initial'
document.body.style.height = 'auto'
}
}
},
methods:{
handle () {
let scrollDom = document.querySelector('.rw_content')
var scrollTop =scrollDom.scrollTop
var offsetTop = document.querySelector('#scrollImg').offsetTop + 126
if (scrollTop > offsetTop) {
this.fixedheader = true
} else {
this.fixedheader = false
}
},
resetdata () {
this.reset()
this.flag = 0
this.getrecruiment()
this.close()
},
close () {
this.maskdialog = false
this.ageShow = false
this.timeShow = false
this.postShow = false
this.$refs.screening.certicateshow = false
},
reset () {
this.endtext = ''
this.orderId = ''
this.allCompList = []
this.total = 0
},
loadBottom: function() {
if (!this.endtext) {
this.loading = true;
}
this.orderId = this.allCompList[this.allCompList.length - 1].orderId
if (this.orderId == 0) {
this.loading = false;
this.allLoaded = true;
return;
}

if (this.flag == 0) {

this.getrecruiment()
} else if (this.flag == 1) {
this.chooseCity ()
} else if (this.flag == 2) {
this.sureMajor ()
} else if (this.flag == 3) {
this.getimeslect()
} else if (this.flag == 4) {
this.chooseage()
}

},
handleBottomChange(val) {
this.bottomText = val;
},

getrecruiment () {
let data = {
"pagesize": "10",
"pageLastId": this.orderId,
};

allRecruitList(data).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allCompList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allCompList.push(item)
})
}, 600)
}
}
})
},


closeother () {
this.ageShow = false
this.timeShow = false
this.postShow = false
this.maskdialog = false
this.$refs.screening.certicateshow = false
},

post(){





this.postShow = true;
this.$refs.screening.certicateshow = true
this.timeShow = false;
this.ageShow = false;
this.maskdialog = true
this.barWidth = "15px";
},

timeType(){





this.timeShow = true
this.$refs.screening.certicateshow = false
this.$refs.screening.nowIndexes = []
this.$refs.screening.selected = []
this.ageShow = false;
this.maskdialog = true
this.barWidth = "15px";
},

age(){
this.flag = 4





this.ageShow = true
this.maskdialog = true
this.postShow = false;
this.timeShow = false;
this.$refs.screening.certicateshow = false
this.$refs.screening.nowIndexes = []
this.$refs.screening.selected = []

this.barWidth = "15px";
},
sexSelect(item,index){

this.nowIndex = index;
this.sex = item.key
},
sureAge(){
this.ageShow = false
this.maskdialog = false



this.reset()

this.chooseage()

},
chooseage () {
let data = {
"pagesize": 10,
"sex": this.sex,
"pageLastId": this.orderId,
};
allRecruitList(data).then(res => {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allCompList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allCompList.push(item)
})
}, 600)
}
})
},






























showDialog(){
this.dialogShow = true;
},

getCity(res){
this.flag = 1
this.reset()
this.dialogShow = false;

this.city = res.city.code;
this.chooseCity ()
},
chooseCity () {
let data = {
"pagesize": "10",
"city": this.city,
"pageLastId": this.orderId,
};
allRecruitList(data).then(res => {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allCompList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allCompList.push(item)
})
}, 600)
}
});
},


postSelect(index,itemer){
this.currentIndex = index;
},

timeSelect(item,index){

this.nowIndex = index;
if(item == '全职'){
this.workType = 1
}else if(item == '兼职'){
this.workType = 2
}else{
this.workType = 0
}
},
suretime () {

this.maskdialog = false
this.timeShow = false
this.reset()

this.flag = 3
this.getimeslect()
},
getimeslect () {

let data = {
"pagesize": "10",
"workingType": this.workType,
"pageLastId": this.orderId,
};
allRecruitList(data).then(res => {

let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allCompList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allCompList.push(item)
})
}, 600)
}

});

},
majorSelect(name,id){
this.likeMajors.push(id);

},

goPostDetail(fid){
this.$router.push({name:'postDetail',query:{id: fid}})
},

postchange (val) {
this.maskdialog = false
this.reset()
this.flag = 2
this.likeMajors = val
this.sureMajor()
},
sureMajor(){
let data = {
"pagesize": 10,
"certMajorL2IdList": this.likeMajors,
"pageLastId": this.orderId,
};
allRecruitList(data).then(res => {
let lists = res.data.datas.list
if (lists.length == 0) {

this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {
lists.forEach(item => {
this.allCompList.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.allCompList.push(item)
})
}, 600)
}
});
this.postShow = false;
},
}
}



