
import { listByTypeAndPid } from '@/api/dictionary.js'
import { addMajory } from '@/api/ucenter.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
XButton,
Flexbox,
FlexboxItem,
XInput,
Group,
Alert
},
directives: {
TransferDom
},
data () {
return {
certicateshow: false,
name: '',
certificatelists: '',
rightlists: '',
showleft: false,
majorL1Id:'',
majorL1Name: '',
majorL2Id: '',
majorL2Name: '',
queryData: {},
nowIndex: '-1',
leftnowIndex: '-1',
messageShow: false,
resMessage: '',
}
},
created () {
this.getcertificate()
},
methods: {
show(){
this.certicateshow = true
},

getcertificate () {
listByTypeAndPid(0,0).then(res => {
if (res.data.result == 0) {
this.certificatelists = res.data.datas
}
})
},
choose (item,index) {
this.leftnowIndex = index
this.showleft = true
listByTypeAndPid(0,item.id).then(res => {
if (res.data.result == 0) {
this.rightlists = res.data.datas;

this.majorL1Id = item.id;
this.majorL1Name = item.name;

}
})
},
chooseSon(item, index){
this.nowIndex = index
this.majorL2Id = item.id;
this.majorL2Name = item.name;

this.queryData = {
majorL1Id:this.majorL1Id,
majorL1Name: this.majorL1Name,
majorL2Id: this.majorL2Id,
majorL2Name: this.majorL2Name
};
this.$emit('postchange',this.queryData)



this.certicateshow = false
},

addNewMajory () {
if (this.name == '') {
this.messageShow = true;
this.resMessage = '请填写岗位名称'
return
}
let id = this.certificatelists[this.leftnowIndex].id
let data = {
name: this.name,
pid: id,
type: 0
};
addMajory(data).then(res => {

listByTypeAndPid(0, id).then(res => {
if (res.data.result == 0) {
this.rightlists = res.data.datas;

this.nowIndex = this.rightlists.length - 1

this.majorL2Id = this.rightlists[this.nowIndex].id
this.majorL2Name =  this.rightlists[this.nowIndex].name

this.queryData = {
majorL1Id:this.majorL1Id,
majorL1Name: this.majorL1Name,
majorL2Id: this.majorL2Id,
majorL2Name: this.majorL2Name
};
this.name = ''


}
})
})

},
goBack (){
this.certicateshow = false
this.$emit('postchange',this.queryData)
}

}
}



