
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { endianness } from 'os';

export default {
components: {
Datetime,
Group
},
data () {
return {
startime: '',
endtime: ''
}

},
methods: {
startchange (val) {
this.startime = val

this.$emit('timechange', 0, val)
},
endchange (val) {
this.endtime = val

this.$emit('timechange', 1, val)
}
}
}



