
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { createcommonDic } from '@/api/dictionary.js'
import { listByTypeAndPid } from '@/api/dictionary'
import { addMajory } from '@/api/ucenter.js'

export default {
components: {
Group,
XInput,
XButton,
Flexbox,
FlexboxItem,
Alert
},
directives: {
TransferDom
},
data () {
return {
messageShow: false,
resMessage: '',
name: '',
byShow: false,
nowIndex: -1,
postname: []
}
},
created(){
this.getpostname()
},
methods: {
chooseitem (val,index) {
this.nowIndex = index
this.byShow = false
this.$emit('choosename',val)
},
getpostname () {
listByTypeAndPid(7,0).then(res => {
if (res.data.result == 0) {
this.postname = res.data.datas
}
})
},
addstationame () {
if (this.name == '') {
this.messageShow = true;
this.resMessage = '请填写岗位名称'
return
}
let data = {
name: this.name,
pid: 0,
type: 7
};
addMajory(data).then(res => {

this.getpostname()
this.name = ''
})

},
}
}



