
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Group from 'vux/src/components/group/index.vue'


export default {
props: {
a: {
type: Boolean,
default: false
},
showonesite: {
type: Boolean,
default: false
},
showtitle: {
type: Boolean,
default: false
}
},
components: {
Group,
XAddress,
},
data () {
return {
addressData: ChinaAddressV4Data,
value: [],
title: '请选择',
onesite: '',
data: {}
}
},
watch: {
value (val) {
if (val) {
if (this.showtitle == true) {

} else {
this.title = ''
}

if (this.showonesite) {
this.$nextTick( () => {
let span = document.querySelector('.vux-cell-value')
if ( this.$refs.address.nameValue.split(' ')[1] == '市辖区') {
span.innerHTML =  this.$refs.address.nameValue.split(' ')[0]
return
}
span.innerHTML =  this.$refs.address.nameValue.split(' ')[1]
})

}
this.$emit('sitechange', this.data)
}
}
},
methods: {
change () {

},
sitechange (id,name) {
this.data = {
province: {
code: id[0],
value: name[0]
},
city: {
code: id[1],
value: name[1]
},
area: {
code: id[2],
value: name[2]
}
}

},
logShow (val) {
this.$emit('closeother')
}

},

}



